import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
export default{
	components: {
		uniLoadMore
	},
	data() {
		return {
			data:'',
			show:0,
			top_class:'',
			video_list:[],
			type:'',
			more: 'more',
			page: 1,
			loadRecord: true,
			v_pid:'',
			v_test:'全部'
		}
	},
	methods:{
		Index(){
			this.more = 'loading'
			let data = {
				type:this.type,
				limit:10,
				page:this.page
			}
			if(this.v_pid) data.v_pid = this.v_pid
			this.uni_request(data)
		},
		chiose(v_pid,test){
			this.page = 1
			this.loadRecord = true
			let data = {
				type:this.type,
				limit:10,
				page:this.page
			}
			if(v_pid){
				data.v_pid = v_pid
				this.v_pid = v_pid
				this.v_test = test
			}else{
				this.v_pid = ''
				this.v_test = '全部'
			}
			this.video_list.length = 0
			
			this.uni_request(data)
			this.show = false
		},
		uni_request(data){
			this.service.entire(this,'get',this.APIconfig.api_root.com_page.videoList,data,function(self,res){
				self.top_class = res.data.top_list
				let data = self.video_list
				data.push(...res.data.video_list)
				self.video_list = data
				console.log(self.video_list);
				self.page += 1
				self.more = 'more'
				if (res.data.video_list.length < 10) {
					self.more = 'noMore'
					self.loadRecord = false
					return
				}
			})
		}
	},
	onLoad(e) {
		this.type = e.type
		this.Index()
	},
	onReachBottom() {
		if (this.loadRecord == false) return
		this.Index()
	},
}
